@import '../../App.css';

.App {
    display: flex;
    justify-content: center;
  }
  .container {
    display: flex;
    width: 100vw;
    height: 100vh;
  }
  .text {
    position: absolute;
    width: 100vw;
    display: flex;
    height: 100vh;
    align-items: center;
    flex-direction: column;
    z-index: 1;
  }
  .text h1 {
    color:#C4C4C4;
    margin: 20px
  }
  
  .brand {
    position: absolute;
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: flex-end;
    justify-content: flex-end;
    z-index: 1;
  }
  .brand h1 {
    color:#C4C4C4;
    margin: 40px
  }
  .collection {
    position: absolute;
    width: 100px;
    height: 40px;
    background-color: var(--theme-color);
    margin-top: 100px;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10px;
  }
  
  .collection div {
    color: #C4C4C4;
    font-size: 1.50rem;
    
  }
  .images{
    display: flex;
    width: 100%;
    height: 100%;
    
  }
  .background {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: black;
    opacity: .5;
  }
  
  .image {
    height: 100%;
    width: 100%;
  }
  .blur {
    width: 100%;
    height: 100%;
    backdrop-filter: blur(30px);
  }
  
  .img1 {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover; 
    
    background-image: url('../../assets/img1.webp');
    /* margin-top: -10px;
    margin-left: -10px;
    margin-right: -10px; */
  }
  .img2 {
    background-image: url('../../assets/https___hypebeast.com_image_2017_12_marcelo-burlon-county-milan-pre-fall-2018-lookbook-1.jpg');
    background-size: cover;
    background-position: center;
  
  }
  .img3 {
    background-image: url('../../assets/https___hypebeast.com_image_2014_09_marcelo-burlon-county-of-milan-2014-fall-winter-womens-lookbook-1.jpg');
    background-size: cover; 
    background-position: center;
   
  }
  .img4 {
    background-image: url('../../assets/https___hypebeast.com_wp-content_blogs.dir_11_files_2019_06_look-2-web.jpg');
    background-size: cover;
    background-position: center;
    
